import { LoadingButton } from "@mui/lab";
import { Box, DialogContent, Grid, Stack, Typography } from "@mui/material";
import TextField from "../../../../components/TextField";
import Vector from "./vectors/decision-making.svg";


const NameView = ({
    values = {},
    onChange,
    onNext
}) => {
    return (
        <DialogContent sx={{ p: 0 }}>
            <Grid container>
                <Grid item xs={12} md={6}>
                    <Box sx={theme => ({
                        background: { md: theme.palette.primary.main },
                        py: { xs: 4, md: 0 },
                        width: '100%',
                        height: '100%',
                        display: 'flex'
                    })}>
                        <Box component='img' sx={{ maxWidth: '65%', m: 'auto' }} src={Vector} alt="" />
                    </Box>
                </Grid>
                <Grid item xs={12} md={6}>
                    <Stack direction="column" alignItems="flex-start" sx={{ p: { xs: 3, md: 7 }, minHeight: "400px" }}>
                        <Typography variant="h6" sx={{ mb: 3 }}>مشخصات کسب و کار</Typography>

                        <Box sx={{ width: '100%' }}>
                            <TextField
                                InputLabelProps={{ shrink: true }}
                                fullWidth={true}
                                size="small"
                                label="نام میز کار"
                                variant="filled"
                                autoFocus
                                value={values.name}
                                onChange={e => onChange("name", e.target.value)}
                            />
                            <TextField
                                InputLabelProps={{ shrink: true }}
                                fullWidth={true}
                                size="small"
                                label="شناسه میز کار"
                                variant="filled"
                                dir="ltr"
                                sx={{ mt: 2 }}
                                value={values.identifier}
                                onChange={e => onChange("identifier", e.target.value)}
                            />
                        </Box>

                        <Box sx={{ mt: 'auto', width: '100%' }}>
                            <Box sx={{ display: 'flex' }}>
                                <LoadingButton
                                    disabled={!Object.keys(values).every(key => Boolean(values[key]))}
                                    onClick={onNext}
                                    variant="contained"
                                    size="large"
                                >ایجاد میز کار</LoadingButton>
                                {/* <Box sx={{ flexGrow: 1 }} /> */}
                                {/* <Button
                                    // disabled={Boolean(selectedCategory)}
                                    // onClick={() => setGoingNext(true)}
                                    // variant="contained"
                                    size="large"
                                >رد کردن</Button> */}
                            </Box>
                        </Box>
                    </Stack>
                </Grid>
            </Grid>
        </DialogContent>
    )
}

export default NameView;