import { LoadingButton } from "@mui/lab";
import { Box, DialogContent, Grid, Stack, Typography } from "@mui/material";
import Vector from "./vectors/decision-making.svg";


const FinalizeView = ({ onDone }) => {
    return (
        <DialogContent sx={{ p: 0 }}>
            <Grid container>
                <Grid item xs={12} md={6}>
                    <Box sx={theme => ({
                        background: { md: theme.palette.primary.main },
                        py: { xs: 4, md: 0 },
                        width: '100%',
                        height: '100%',
                        display: 'flex'
                    })}>
                        <Box component='img' sx={{ maxWidth: '65%', m: 'auto' }} src={Vector} alt="" />
                    </Box>
                </Grid>
                <Grid item xs={12} md={6}>
                    <Stack direction="column" alignItems="flex-start" sx={{ p: { xs: 3, md: 7 }, minHeight: "400px" }}>
                        <Typography variant="h6" sx={{ mb: 3 }}>میز کار با موفقیت ایجاد گردید</Typography>

                        <Box sx={{ mt: 'auto', width: '100%' }}>
                            <Box sx={{ display: 'flex' }}>
                                <LoadingButton
                                    // disabled={!Object.keys(values).every(key => Boolean(values[key]))}
                                    onClick={onDone}
                                    variant="contained"
                                    size="large"
                                >پایان و بستن</LoadingButton>
                            </Box>
                        </Box>
                    </Stack>
                </Grid>
            </Grid>
        </DialogContent>
    )
}

export default FinalizeView;