import { Box, Button, DialogContent, Grid, Paper, Stack, Typography, styled } from "@mui/material";
import Vector from "./vectors/decision-making.svg";

const PERIODS = [
    { name: "آزمایشی", value: "demo" },
    { name: "شش‌ ماهه", value: "semi-annually" },
    { name: "یک‌ ساله", value: "annually" },
]

const STATUS = [
    { name: "پرداخت شده", value: "paid" },
    { name: "پرداخت نشده", value: "unpaid" },
]


const LabelItem = styled(Paper, {
    shouldForwardProp: prop => prop !== 'selected'
})(({ theme, selected }) => ({
    display: 'inline-block',
    padding: theme.spacing(1, 3),
    marginBottom: theme.spacing(1),
    cursor: "pointer",
    outline: '1px solid transparent',
    transition: theme.transitions.create('all'),

    '&:not(first-of-type)': {
        marginLeft: theme.spacing(1)
    },

    ...(selected && ({
        borderColor: theme.palette.primary.main,
        outlineColor: theme.palette.primary.main,
        '& .MuiTypography-root': {
            fontWeight: 700
        }
    }))
}));

const PaymentInfoView = ({
    values = { period: '', payment_status: '' },
    onChange,
    onNext,
    loading
}) => {
    return (
        <DialogContent sx={{ p: 0 }}>
            <Grid container>
                <Grid item xs={12} md={6}>
                    <Box sx={theme => ({
                        background: { md: theme.palette.primary.main },
                        py: { xs: 4, md: 0 },
                        width: '100%',
                        height: '100%',
                        display: 'flex'
                    })}>
                        <Box component='img' sx={{ maxWidth: '65%', m: 'auto' }} src={Vector} alt="" />
                    </Box>
                </Grid>
                <Grid item xs={12} md={6}>
                    <Stack direction="column" alignItems="flex-start" sx={{ p: { xs: 3, md: 7 }, minHeight: "400px" }}>
                        <Typography variant="h6" sx={{ mb: 2 }}>دوره ارائه برنامه‌ها</Typography>

                        <Box sx={{ width: '100%' }}>
                            {PERIODS.map((period, index) => (
                                <LabelItem
                                    key={index}
                                    variant="outlined"
                                    selected={values.period == period.value}
                                    onClick={() => onChange({ ...values, period: period.value })}
                                >
                                    <Typography>{period.name}</Typography>
                                </LabelItem>
                            ))}
                        </Box>

                        <Typography variant="h6" sx={{ my: 2 }}>وضعیت پرداخت</Typography>

                        <Box sx={{ width: '100%' }}>
                            {STATUS.map((status, index) => (
                                <LabelItem
                                    key={index}
                                    variant="outlined"
                                    selected={values.payment_status == status.value}
                                    onClick={() => onChange({ ...values, payment_status: status.value })}
                                >
                                    <Typography>{status.name}</Typography>
                                </LabelItem>
                            ))}
                        </Box>

                        <Box sx={{ mt: 'auto' }}>
                            <Button
                                // disabled={!Boolean(value)}
                                onClick={onNext}
                                variant="contained"
                                size="large"
                            >مرحله بعد</Button>
                        </Box>
                    </Stack>
                </Grid>
            </Grid>
        </DialogContent>
    )
}

export default PaymentInfoView;