import { Box, Button, CircularProgress, DialogContent, Grid, Paper, Stack, Typography, styled } from "@mui/material";
import { useState } from "react";
import { useMiddleware } from "../../../../Application";
import { useEffect } from "react";
import Vector from "./vectors/decision-making.svg";


const CategoryItem = styled(Paper, {
    shouldForwardProp: prop => prop !== 'selected'
})(({ theme, selected }) => ({
    display: 'inline-block',
    padding: theme.spacing(1, 3),
    marginBottom: theme.spacing(1),
    cursor: "pointer",
    outline: '1px solid transparent',
    transition: theme.transitions.create('all'),

    '&:not(first-of-type)': {
        marginLeft: theme.spacing(1)
    },

    ...(selected && ({
        borderColor: theme.palette.primary.main,
        outlineColor: theme.palette.primary.main,
        '& .MuiTypography-root': {
            fontWeight: 700
        }
    }))
}));

const CategoriesFutureList = ({ selected, onSelect }) => {

    const [loading, setLoading] = useState(true);
    const [data, setData] = useState([]);
    const categoriesMiddleware = useMiddleware('Categories.Main');

    const loadCategories = async () => {
        const response = await categoriesMiddleware.list({
            label: 'workspace'
        }, {
            transformRequest: [(data, headers) => {
                delete headers.common['Workspace-ID'];
                return data;
            }]
        });
        setLoading(false);
        setData(response.data);
    }

    useEffect(() => {
        loadCategories();
    }, []);


    if (loading) {
        return (
            <Box sx={{ width: '100%', py: 5, display: 'flex' }}>
                <CircularProgress sx={{ mx: 'auto' }} />
            </Box>
        )
    }

    return (
        <Box sx={{ width: '100%', maxHeight: 200, overflow: 'auto' }}>
            {data.map((category, index) => (
                <CategoryItem
                    key={index}
                    variant="outlined"
                    selected={selected && selected.id === category.id}
                    onClick={() => onSelect(category)}
                >
                    <Typography>{category.name}</Typography>
                </CategoryItem>
            ))}
        </Box>
    )
}

const CategoryView = ({
    value,
    onChange,
    onNext,
    loading
}) => {
    return (
        <DialogContent sx={{ p: 0 }}>
            <Grid container>
                <Grid item xs={12} md={6}>
                    <Box sx={theme => ({
                        background: { md: theme.palette.primary.main },
                        py: { xs: 4, md: 0 },
                        width: '100%',
                        height: '100%',
                        display: 'flex'
                    })}>
                        <Box component='img' sx={{ maxWidth: '65%', m: 'auto' }} src={Vector} alt="" />
                    </Box>
                </Grid>
                <Grid item xs={12} md={6}>
                    <Stack direction="column" alignItems="flex-start" sx={{ p: { xs: 3, md: 7 }, minHeight: "400px" }}>
                        <Typography variant="h6" sx={{ mb: 3 }}>دسته کسب و کار</Typography>

                        <CategoriesFutureList
                            selected={value}
                            onSelect={onChange}
                        />

                        <Box sx={{ mt: 'auto' }}>
                            <Button
                                disabled={!Boolean(value)}
                                onClick={onNext}
                                variant="contained"
                                size="large"
                            >مرحله بعد</Button>
                        </Box>
                    </Stack>
                </Grid>
            </Grid>
        </DialogContent>
    )
}

export default CategoryView;