import AddIcon from "@mui/icons-material/Add";
import CategoryOutlined from "@mui/icons-material/CategoryOutlined";
import MenuOutlined from "@mui/icons-material/MenuOutlined";
import { Box, Card, Stack } from "@mui/material";
import axios from "axios";
import flatten from "lodash/flatten";
import sortBy from "lodash/sortBy";
import { useSnackbar } from "notistack";
import { useSearchParams } from "react-router-dom";
import { useComponent, useMiddleware, useTask } from "../../../../Application";
import { useAuth } from "../../../../auth";
import ResponsiveActionBar from "../../../../components/ResponsiveActionBar";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";
import useDialogProps from "../../../../utils/useDialogProps";
import useNavigateTask from "../../../../utils/useNavigateTask";
import useResponsiveTableFetch from "../../../../utils/useResponsiveTableFetch";
import CreateWorkspaceDialog from "../../components/CreateWorkspaceDialog";
import DataTable from "./DataTable";


const Workspaces = () => {

    const { inject } = useAuth();

    const middleware = useMiddleware('Core.workspaces');

    const [searchParams] = useSearchParams();
    const navigateTask = useNavigateTask();

    const { data, total, loading, loadMore, reload } = useResponsiveTableFetch({
        handleFetch: params => middleware.list({
            ...params,
            ...Object.fromEntries([...searchParams])
        }),
        loadDeps: [searchParams],
        infiniteScrollDefaultParams: {
            page: searchParams.has('page') ? searchParams.get('page') : 0,
            perPage: searchParams.has('perPage') ? searchParams.get('perPage') : 25,
        }
    });

    const CategoriesDialog = useComponent('Categories.CategoriesDialog');
    const [categoriesDialogProps, openCategoriesDialog, closeCategoriesDialog] = useDialogProps("categories");


    const { enqueueSnackbar } = useSnackbar();

    const [formDialogProps, openFormDialog, closeFormDialog] = useDialogProps("form");
    const handleCreateOrUpdate = (message) => {
        enqueueSnackbar(message);
        reload();
        closeFormDialog();
    }


    const injectedHooks = Object.assign({}, ...(inject('People', 'PeopleList.Hooks') || []));

    const task = useTask('Core.manageWorkspaces');


    return (
        <TabView {...task}>
            <ResponsiveContainer>
                <Box
                    sx={{
                        display: "flex",
                        mb: { xs: 0, sm: 1 }
                    }}
                >
                    <Box sx={{ flexGrow: 1 }} />
                    <Stack direction="row" spacing={1}>
                        <ResponsiveActionBar
                            actions={sortBy([
                                {
                                    title: 'میز کار جدید',
                                    icon: <AddIcon />,
                                    onClick: () => {
                                        openFormDialog({
                                            onCreate: () => handleCreateOrUpdate(`میز کار با موفقیت ثبت گردید.`)
                                        })
                                    },
                                    authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('workspace:edit'),
                                    priority: 100
                                }, {
                                    title: 'مدیریت گروه‌ها',
                                    icon: <CategoryOutlined fontSize="inherit" />,
                                    onClick: () => openCategoriesDialog()
                                },
                                ...flatten((inject('Core', 'WorkspacesList.ActionsList', { data, loading, reload, ...injectedHooks }) || []))
                            ], o => o.priority)}
                            ResponsiveActionIcon={<MenuOutlined />}
                            responsiveActionProps={{ color: 'primary' }}
                        />
                    </Stack>
                </Box>
                <Card variant="outlined" sx={{ flexGrow: 1 }}>
                    <DataTable
                        data={data}
                        loading={loading}
                        meta={{ total }}
                        infiniteScrollProps={{
                            loading: false,
                            hasNextPage: data.length < total,
                            onLoadMore: loadMore
                        }}
                    // onClickEdit={item => {
                    //     openFormDialog({
                    //         defaultValues: item,
                    //         onCreateOrUpdate: () => handleCreateOrUpdate(`مشخصات میز کار بروزرسانی گردید.`)
                    //     })
                    // }}
                    // onClickDelete={(person, permanent) => {
                    //     openDeleteDialog({ person, permanent });
                    // }}
                    />
                </Card>

                <CreateWorkspaceDialog {...formDialogProps} />

                <CategoriesDialog
                    label="workspace"
                    {...categoriesDialogProps}
                    requestConfig={{
                        transformRequest: [(data, headers) => {
                            delete headers.common['Workspace-ID'];
                            return data;
                        }, ...axios.defaults.transformRequest]
                    }}
                />

                {inject('Core', 'WorkspacesList.Footer', { data, loading, reload, ...injectedHooks })}
            </ResponsiveContainer>
        </TabView>
    )
}

export default Workspaces;