import map from "lodash/map";
import { useState } from "react";
import { useMiddleware } from "../../../../Application";
import ResponsiveDialog from "../../../../components/ResponsiveDialog";
import AppsView from "./AppsView";
import CategoryView from "./CategoryView";
import FinalizeView from "./FinalizeView";
import MembersView from "./MembersView";
import NameView from "./NameView";
import PaymentInfoView from "./PaymentInfoView";


const Main = ({ open, onClose, onCreate }) => {

    const [viewIndex, setViewIndex] = useState(0);
    const goNext = () => setViewIndex(viewIndex + 1);
    const getPrev = () => setViewIndex(viewIndex - 1);

    const [formData, setFormData] = useState({
        category: null,
        name: '',
        identifier: ''
    });
    const getValue = (key, defaultValue = '') => formData[key] || defaultValue;
    const setValue = (key, value) => setFormData({ ...formData, [key]: value });


    const [loading, setLoading] = useState(false);
    const workspacesMiddleware = useMiddleware('Core.Workspaces');

    const [workspace, setWorkspace] = useState(null);
    const _createWorkspace = async () => {
        setLoading(true);
        const response = await workspacesMiddleware.create(formData);
        setWorkspace(response.workspace);
        setLoading(false);
        goNext();
    }

    const [subscriptionsFormData, setSubscriptionsFormData] = useState({
        apps: [],
        period: 'annually',
        payment_status: 'paid'
    });

    const _subscribeToApps = async () => {
        setLoading(true);
        const response = await workspacesMiddleware.subscribe(workspace.id, {
            modules: map(subscriptionsFormData.apps, 'id'),
            period: subscriptionsFormData.period,
            payment_status: subscriptionsFormData.payment_status,
        });
        setLoading(false);
        goNext();
    }

    const [membersFormData, setMembersFormData] = useState({
        first_name: '',
        last_name: '',
        phone: ''
    });
    const getMembersFormValue = (key, defaultValue = '') => membersFormData[key] || defaultValue;
    const setMembersFormValue = (key, value) => setMembersFormData({ ...membersFormData, [key]: value });

    const _addOwnerMember = async () => {
        setLoading(true);
        const response = await workspacesMiddleware.addOwner(workspace.id, membersFormData);
        setLoading(false);
        goNext();
    }


    return (
        <ResponsiveDialog
            open={open}
            onClose={onClose}
            dialogProps={{
                fullWidth: true,
                maxWidth: "md",
                PaperProps: {
                    sx: { border: 'none' }
                },
                BackdropProps: {
                    style: {
                        backdropFilter: 'blur(3px)'
                    }
                }
            }}
        >
            {/* <DialogTitle sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Box sx={{ flexGrow: 1 }} />
                <IconButton onClick={onClose} size="small">
                    <CloseIcon />
                </IconButton>
            </DialogTitle> */}

            {
                viewIndex === 0 && (
                    <CategoryView
                        value={getValue("category", null)}
                        onChange={category => setValue("category", category)}
                        onNext={goNext}
                    />
                )
            }

            {
                viewIndex === 1 && (
                    <NameView
                        values={{ name: formData.name, identifier: formData.identifier }}
                        onChange={(key, value) => setValue(key, value)}
                        onNext={_createWorkspace}
                    />
                )
            }

            {
                viewIndex === 2 && (
                    <AppsView
                        value={subscriptionsFormData.apps}
                        onChange={(apps) => setSubscriptionsFormData({ ...subscriptionsFormData, apps })}
                        onNext={goNext}
                    />
                )
            }

            {
                viewIndex === 3 && (
                    <PaymentInfoView
                        values={subscriptionsFormData}
                        onChange={(values) => setSubscriptionsFormData({ ...subscriptionsFormData, ...values })}
                        onNext={_subscribeToApps}
                    />
                )
            }

            {
                viewIndex === 4 && (
                    <MembersView
                        values={membersFormData}
                        onChange={(key, value) => setMembersFormValue(key, value)}
                        onNext={_addOwnerMember}
                    />
                )
            }

            {
                viewIndex === 5 && (
                    <FinalizeView onDone={onCreate} />
                )
            }
        </ResponsiveDialog >
    )
}

export default Main;