import { Box, Button, CircularProgress, DialogActions, DialogContent, Divider, Grid, Paper, Stack, Tab, Typography, styled } from "@mui/material";
import { useState } from "react";
import { useMiddleware } from "../../../../Application";
import { useEffect } from "react";
import { TabContext, TabList, TabPanel } from "@mui/lab";
import Text from "../../../../components/Text";
import { sumBy } from "lodash";
import pricify from "../../../../utils/pricify";


const AppItem = styled(Paper, {
    shouldForwardProp: prop => prop !== 'selected'
})(({ theme, selected }) => ({
    minHeight: 100,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'flex-end',
    padding: theme.spacing(2),
    marginBottom: theme.spacing(1),
    cursor: "pointer",
    outline: '1px solid transparent',
    transition: theme.transitions.create('all'),

    '&:not(first-of-type)': {
        marginLeft: theme.spacing(1)
    },

    ...(selected && ({
        borderColor: theme.palette.primary.main,
        outlineColor: theme.palette.primary.main,
        '& .MuiTypography-root': {
            fontWeight: 700
        }
    }))
}));

const AppsFutureList = ({ value = [], onChange = () => { } }) => {

    const [loading, setLoading] = useState(true);
    const [data, setData] = useState([]);
    const modulesMiddleware = useMiddleware('Core.Modules');

    const loadApps = async () => {
        const response = await modulesMiddleware.list({}, {
            transformRequest: [(data, headers) => {
                delete headers.common['Workspace-ID'];
                return data;
            }]
        });
        setLoading(false);
        setData(response.data);
    }

    useEffect(() => {
        loadApps();
    }, []);

    const valueIncludesApp = app => value.filter(itm => itm.id === app.id).length > 0;
    const getChangedValue = app => {
        if (valueIncludesApp(app)) {
            return value.filter(val => val.id !== app.id);
        }
        return [...value, app];
    }


    if (loading) {
        return (
            <Box sx={{ textAlign: 'center' }}>
                <CircularProgress />
            </Box>
        )
    }

    return (
        <Grid container>
            {data.map((app, index) => (
                <Grid item xs={6} sm={4} md={3} key={index}>
                    <AppItem
                        variant="outlined"
                        selected={value.filter(itm => itm.id === app.id).length > 0}
                        onClick={() => onChange(getChangedValue(app))}
                    >
                        <Typography>{app.name}</Typography>
                    </AppItem>
                </Grid>
            ))}
        </Grid>
    )
}

const AppsView = ({
    value,
    onChange,
    onNext
}) => {

    // const [activeTab, setActiveTab] = useState('bundles');
    // const handleChange = (event, newTab) => setActiveTab(newTab);

    const getTotal = () => {
        return pricify(sumBy(value, val => val.monthlyCost && val.monthlyCost['IRR']));
    }


    return (
        <>
            <DialogContent>
                <Typography variant="h6" sx={{ mb: 3 }}>انتخاب برنامه‌ها</Typography>

                <AppsFutureList
                    value={value}
                    onChange={onChange}
                />

                {/* <TabContext value={activeTab}>
                <Box>
                    <TabList
                        onChange={handleChange}
                        centered
                    >
                        <Tab label="بسته‌ها" value="bundles" />
                        <Tab label="برنامه‌ها" value="apps" />
                    </TabList>
                </Box>
                <TabPanel value="bundles">
                    Item One
                </TabPanel>
                <TabPanel value="apps">
                    <AppsFutureList
                        value={value}
                        onChange={onChange}
                    />
                </TabPanel>
            </TabContext> */}
            </DialogContent>
            <Divider />
            <DialogActions>
                <Box>
                    <Typography variant="h6" sx={{ m: 0, lineHeight: 1 }}><Text>{getTotal()}</Text></Typography>
                    <Typography variant="caption" sx={{ m: 0, lineHeight: 1 }}>تومان/ماهیانه</Typography>
                </Box>
                <Box sx={{ flexGrow: 1 }} />
                <Button
                    // disabled={!Boolean(selectedCategory)}
                    onClick={onNext}
                    variant="contained"
                    size="large"
                >مرحله بعد</Button>
                <Button
                    sx={{ ml: 1 }}
                    size="large"
                >رد کردن</Button>
            </DialogActions>
        </>
    )
}

export default AppsView;